* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: black;
    font-size: 1.4rem;
    letter-spacing: 0.2rem;
}

.box {
    text-align: center;
    padding: 2rem;
    background-color: gray;
    border: solid 1px green;
    color: white;
}